import React, { useState,useEffect } from 'react'
import { useHistory,useParams } from 'react-router-dom';
import dummy from "../db/data.json";
import {Nav} from 'react-bootstrap';
import '../Detail.scss'; //CSSÌååÏùºÏùÑ Ïó¨Í∏∞Ïóê Ï†ÅÏö©
import {CSSTransition} from "react-transition-group";
import QnA from './QnA';

function GoodsDetail() {
    let history = useHistory();
    let { id } = useParams();
    const [data, setData] = useState([]);
    const [showAlert,setShowAlert] = useState(true);
    const [onTap,setOnTap] = useState(0);
    const [tapSwitch,setTapSwitch] = useState(false);
    
    useEffect(() =>{
        const timer = setTimeout(()=>{setShowAlert(false)},2000);
        return () => {clearTimeout(timer)}
    }, [showAlert]);

    useEffect(()=>{
         setData(dummy.sales.filter( num => {return num.id === Number(id)})[0]);
    },[data]);

    if (data.length === 0) return null;

    
    function TapContent(props){

        useEffect(()=>{
            props.setTapSwitch(true);
        })

        if(props.onTap === 0){
            return <div>ÏÉÅÏÑ∏Ï†ïÎ≥¥</div>
        } else if(props.onTap === 1){
            return <div>Î¶¨Î∑∞</div>
        }else{
            return <QnA></QnA>
        }
    }

    return (
        <div className="container detail">
            
            <div className ="title">
                <h4 className="red">ÏÉÅÌíà ÏÉÅÏÑ∏ÏÑ§Î™Ö</h4>
                <hr/>
            </div>
            {data.stock <= 5 && showAlert === true? 
            <div className="my_alert">
                <p>Ïû¨Í≥†Í∞Ä ÏñºÎßà ÎÇ®ÏßÄ ÏïäÏïòÏäµÎãàÎã§.</p>
            </div> : null}
            <div className="row">
                <div className="col-md-6">
                <img style={{borderRadius:"10px"}} src={data.img} width="100%"/>
                </div>
                <div className="col-md-6 mt-4">
                <h1 className="pt-5">{data.name}</h1>
                <p>{data.desc}</p>
                <hr/>
                <p>{data.price}Ïõê</p>
                <button className="btn btn-danger">Ïû•Î∞îÍµ¨Îãà</button> 
                &nbsp;
                <button className="btn btn-danger">Î∞îÎ°úÍµ¨Îß§</button> 
                &nbsp;
                <button className="btn btn-danger" onClick={()=>{
                    history.goBack();
                }}>ÎèåÏïÑÍ∞ÄÍ∏∞</button> 
                </div>
            </div>

            {/* Tap */}
            <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{setTapSwitch(false); setOnTap(0)}}>ÏÉÅÏÑ∏Ï†ïÎ≥¥</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{setTapSwitch(false); setOnTap(1)}}>Î¶¨Î∑∞</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-2" onClick={()=>{setTapSwitch(false); setOnTap(2)}}>QnA</Nav.Link>
                </Nav.Item>
            </Nav>

            <CSSTransition in={tapSwitch} classNames="ani" timeout={500}>
                <TapContent onTap={onTap} setTapSwitch={setTapSwitch}></TapContent>
            </CSSTransition>
           

        </div> 
    )


}

export default GoodsDetail
